import { useState } from 'react';
type InitialState = { [s: string]: any };

const useForm = (
  initialState: InitialState = {},
): [InitialState, (e: React.SyntheticEvent) => void] => {
  const [state, setState] = useState(initialState);

  const handleChange = (e: React.SyntheticEvent) => {
    const target = e.target as typeof e.target & {
      [s: string]: string;
    };

    let value;
    const { name } = target;

    value = target.type === 'checkbox' ? target.checked : target.value;

    if (target.type === 'select-multiple') {
      const oldValues = state[name];
      value = oldValues.includes(target.value)
        ? oldValues.filter((x: string) => x !== target.value)
        : oldValues.concat(target.value);
    }
    console.log(value);
    setState(state => ({
      ...state,
      [name]: value,
    }));
  };

  return [initialState, handleChange];
};

export default useForm;
